.App {
  text-align: center;
}

header {
  background:rgba(1, 49, 97, 0.45);
  margin-bottom: 3rem;
  transition: all 0.25s ease-out;
}

header:hover {
  background:rgba(1, 49, 97, 0.95);
}

header > h1 {
  font-family: 'MonteCarlo', cursive;
  font-size: 72px;
  color: #0ccac4;
}

header > p {
  color: #0ccac4;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.m-1 {
  margin: 1rem;
}

.m-2 {
  margin: 2rem;
}

.m-3 {
  margin: 3rem;
}

.ml-1 {
  margin-left: 1rem;
}

.filter-section {
  display: flex;
  flex-direction: row !important;
  justify-content: center;
}

.MuiInput-underline::before {
  border-color: #61dafb !important;
}